<template>
  <v-menu>
    <template v-slot:activator="{ props }">
      <v-fade-transition>
        <v-btn
          size="x-large"
          class="btn-bookmark"
          icon="mdi-bookmark"
          color="primary"
          v-bind="props"
        >
        </v-btn>
      </v-fade-transition>
    </template>

    <v-list class="btn-bookmark-menu" density="compact">
      <template v-for="(category, index) in equipmentCommentList" :key="index">
        <v-list-subheader
          >{{ category.category }}
          <span class="text-caption">{{
            category.categorySubTitle
          }}</span></v-list-subheader
        >
        <v-list-item-title :value="index" class="mb-2 ms-3 d-flex flex-row">
          <v-btn
            v-if="category.items.length > 0"
            size="x-small"
            color="primary"
            class="mx-1"
            @click="navTo(category.categoryCode, false)"
            rounded="pill"
            >裝備</v-btn
          >
          <v-btn
            size="x-small"
            color="primary"
            class="mx-1"
            v-if="category.hasCard && category.cards.length > 0"
            @click="navTo(category.categoryCode, true)"
            rounded="pill"
            >卡片</v-btn
          >
        </v-list-item-title>

        <v-divider></v-divider>
      </template>
    </v-list>
  </v-menu>
</template>
<script>
export default {
  name: 'EquipmentCommentNav',
  data: () => ({}),
  props: ['equipmentCommentList'],
  components: {},
  computed: {},
  methods: {
    navTo(categoryCode, isCard) {
      const refName = this.getRefName(categoryCode, isCard);
      //const el = this.$refs[refName][0];
      const el = document.getElementById(refName);
      console.log(el);
      this.$smoothScroll({
        scrollTo: el,
        duration: 300,
        offset: -50,
      });
    },
    getRefName(categoryCode, isCard) {
      let target = '';
      if (!isCard) {
        target = `${categoryCode}-equipment`;
      } else {
        target = `${categoryCode}-card`;
      }
      return target.replace(/-/gi, '_');
    },
  },
};
</script>
<style scoped>
.btn-bookmark {
  position: fixed;
  bottom: 19px;
  right: 85px;
  z-index: 99;
  padding: 0;
  opacity: 70%;
}
.btn-bookmark-menu {
  bottom: 1rem;
}
@media (max-width: 500px) {
  .btn-bookmark {
    bottom: 2px;
    right: 62px;
  }
}
</style>
